<template>
  <div class="pages">
    <el-pagination
      background
      @size-change="onPageSizeChange"
      @current-change="onPageChange"
      :current-page="Number(paginationPage)"
      :total="Number(total)"
      :page-size="Number(paginationPagesize)"
      :page-sizes="[10, 20, 30, 50]"
      layout="sizes, prev, pager, next, jumper"
    >
    </el-pagination>
  </div>
</template>

<script>
import { $on, $off, $once, $emit } from '../../utils/gogocodeTransfer'
export default {
  name: 'PageTool',
  props: ['total', 'paginationPage', 'paginationPagesize'],
  data () {
    return {}
  },
  methods: {
    // 进入某一页
    onPageChange: function (pageNum) {
      $emit(this, 'pageChange', pageNum)
    },
    // 每页显示信息条数
    onPageSizeChange: function (pageSize) {
      $emit(this, 'pageSizeChange', pageSize)
    }
  },
  emits: ['pageChange', 'pageSizeChange']
}
</script>

<style>
.blue {
  color: #409eff;
}
</style>
